.app {
    width: 100%;
    height: 100vh;
    padding: 0;
    margin: 0;
}

.appGrid {
    display: grid;
    grid-template-columns: 70% 30%;
}

.background {
    width: 100%;
    height: 100vh;
    background: url('../assets/background.jpg');
    padding: 10rem;
    color: white;
}

.form {
    width: 100%;
    height: 100vh;
    padding: 5rem;
}

.formContainer {
    padding: .5rem;
}


.forms input[type="email"],
[type="password"],
[type="submit"],
[type="text"],
[type="number"],
select {
    border-radius: 0;
    padding: 1rem;
}

.forms input::placeholder {
    font-size: 13px;

}

.forms label {
    font-size: 12px;
}

.overlay {
    width: 100%;
    height: auto;
    display: block;
    background: rgba(0, 0, 0, 0.8);
    padding: 3rem;
}








/* phone-only */
@media (max-width: 599px) {

    .appGrid {
        grid-template-columns: 100%;
    }

    .background {
        width: 100%;
        height: 20rem;
        padding: 1rem;
    }

    .overlay {
        width: 100%;
        padding: 1rem;
    }

    .overlay p {
        font-size: 10px;
    }

    .form {
        padding: 1rem;
    }

}

