@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', 'sans-serif';
}
.styles_app__2hBow {
    width: 100%;
    height: 100vh;
    padding: 0;
    margin: 0;
}

.styles_appGrid__3_ggN {
    display: grid;
    grid-template-columns: 70% 30%;
}

.styles_background__bd4bw {
    width: 100%;
    height: 100vh;
    background: url(/static/media/background.d9185526.jpg);
    padding: 10rem;
    color: white;
}

.styles_form__3wque {
    width: 100%;
    height: 100vh;
    padding: 5rem;
}

.styles_formContainer__30UJD {
    padding: .5rem;
}


.styles_forms__2Gmd0 input[type="email"],
[type="password"],
[type="submit"],
[type="text"],
[type="number"],
select {
    border-radius: 0;
    padding: 1rem;
}

.styles_forms__2Gmd0 input::-webkit-input-placeholder {
    font-size: 13px;

}

.styles_forms__2Gmd0 input:-ms-input-placeholder {
    font-size: 13px;

}

.styles_forms__2Gmd0 input::placeholder {
    font-size: 13px;

}

.styles_forms__2Gmd0 label {
    font-size: 12px;
}

.styles_overlay__fE-Ba {
    width: 100%;
    height: auto;
    display: block;
    background: rgba(0, 0, 0, 0.8);
    padding: 3rem;
}








/* phone-only */
@media (max-width: 599px) {

    .styles_appGrid__3_ggN {
        grid-template-columns: 100%;
    }

    .styles_background__bd4bw {
        width: 100%;
        height: 20rem;
        padding: 1rem;
    }

    .styles_overlay__fE-Ba {
        width: 100%;
        padding: 1rem;
    }

    .styles_overlay__fE-Ba p {
        font-size: 10px;
    }

    .styles_form__3wque {
        padding: 1rem;
    }

}

